.p-pagination {
  margin: 0 0 20px;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &__item {
    $item: &;
    background-color: #eaeaea;
    width: 30px;
    height: 35px;
    @include font-line(13, 13);
    color: $color-text01;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      width: 100%;
      height: 100%;
      border: none;
      color: $color-text01;
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: $color-accent;
        color: #fff !important;
      }
    }
    + #{$item} {
      margin-left: 5px;
    }
    &.is-active {
      background-color: $color-accent;
      color: #fff;
    }
    &:first-child {
      margin-right: 10px;
    }
    &:last-child {
      margin-left: 15px;
    }
  }
}
