/////////////////////////////////////////////////////////////////////////////////
//content
/////////////////////////////////////////////////////////////////////////////////

.l-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: $wrap-width;
  margin: 0 auto;
  padding: 23px 0 110px;
  position: relative;
  .l-main {
    background: #fff;
    flex: auto;
    max-width: $wrap-width;
    margin: 0 auto;
    padding: 52px 0 0;
    &--contents {
      width: 100%;
      margin: 0;
      padding: 0;
      &__inner {
        border: none;
        margin: 0 auto;
        padding: 0;
      }
    }
  }
  .l-side {
    flex: 0 0 260px;
    margin: 0 0 0 40px;
  }
}

/**/
.l-side {
  .sideSticky {
    position: sticky;
    top: 0;
  }
  &__heading {
    &01 {
      text-align: center;
      padding-top: 20px;
      &__en {
        @include font-line(25, 25);
        font-family: "Noto Sans Japanese", sans-serif;
        font-weight: 700;
        display: block;
        .if {
          padding-right: 5px;
          @include font-line(20);
        }
      }
      &__ja {
        margin: 0 auto 60px;
        @include font-line(16, 16);
        font-family: 'Roboto', sans-serif;
        font-weight: 900;
        text-align: center;
      }
    }
    &02 {
      @include font-line(16, 16);
      font-weight: 700;
      margin: 21px 0;
    }
  }
  .js-accordion {
    position: relative;
    .plus-button {
      display: block;
      background: #000;
      border-radius: 50%;
      height: 24px;
      width: 24px;
      position: absolute;
      top: -4px;
      left: 100px;
      &::after {
        content: '';
        height: 15px;
        width: 3px;
        position: absolute;
        z-index: 1;
        background: #fff;
        top: 5px;
        right: 10px;
      }
      &::before {
        content: '';
        height: 3px;
        width: 15px;
        position: absolute;
        z-index: 1;
        background: #fff;
        top: 11px;
        right: 4px;
      }
    }
    &.is-active {
      .plus-button {
        &::after {
          content: none;
        }
      }
    }
  }
  &__section {
    background: #eaeaea;
    border-radius: 10px;
    margin-bottom: 20px;
    .checkList {
      padding: 0;
    }
    .price {
      &__box {
        text-align: center;
      }
    }
    .tokutyo {
      .js-accordion {
        cursor: pointer;
      }
      .checkList {
        display: none;
      }
    }
  }
  &__box {
    margin: 0;
    padding: 0 20px;
    dl {
      margin: 0;
    }
    dt {
      @include font-line(12);
      text-align: left;
      line-height: 22px;
      font-weight: 700;
    }
    dd {
      margin: 0;
      padding: 0;
      margin-bottom: 10px;
    }
    .button {
      @include font-line(14,14);
      width: 150px;
      border-radius: 20px;
      margin: 0 auto;
      i {
        @include font-line(16);
        padding-right: 5px;
      }
    }
    .linkText {
      @include font-line(13,15);
      border-bottom: 2px solid #b4b4b4;
      text-decoration: none;
      color: $color-text01;
      font-weight: 700;
    }
  }
  &__bnr {
    margin-bottom: 20px;
    .button {
      width: 100%;
      margin-bottom: 10px;
      @include font-line(18);
      i {
        @include font-line(26);
        padding-right: 10px;
      }
    }
  }
}
