.l-breadcrumb {
  margin: 0;
  padding: 0;
  text-align: left;
  line-height: 13px;
  position: absolute;
  top: 22px;
  left: 0;
  &__item {
    $this: &;
    @include font-line(13, 13);
    display: inline-block;
    vertical-align: middle;
    + #{$this} {
      &::before {
        content: "";
        font-weight: 500;
        padding: 0 4px;
      }
    }
    a {
      border: none;
      color: $color-text01;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
