.c-list {
  margin: 0;
  padding: 0;
  list-style: none;
  &__item {
    padding: 0 0 0 28px;
    list-style: none;
    color: $color-text01;
    position: relative;
    &::before {
      content: "";
      background-color: $color-main;
      border-radius: 50%;
      width: 13px;
      height: 13px;
      margin: auto;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 9px;
    }
    &::after {
      content: "";
      background-color: #fff;
      border-radius: 50%;
      width: 7px;
      height: 7px;
      margin: auto;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 12px;
    }
  }
}

.c-dl {
  border-top: 1px solid #d6cfc8;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  &__heading,
  &__data {
    border-bottom: 1px solid #d6cfc8;
    height: 55px;
    @include font-line(15, 15);
    color: $color-text01;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  &__heading {
    background-color: #fff;
    flex: 0 0 25%;
    padding: 0 0 0 27px;
    font-weight: 700;
  }
  &__data {
    background-color: #fff;
    flex: 0 0 75%;
    margin: 0;
    padding: 0;
    font-weight: 500;
  }
}
