.l-footer {
  background-color: $color-main;
  padding: 50px 0 49px;
  &__navi {
    padding: 0;
    margin: 0 auto;
    text-align: center;
    display: flex;
    justify-content: space-between;
    .item {
      @include font-line(12, 12);
      color: #fff;
      display: inline-block;
      flex: 0 0 16.6666666%;
      position: relative;
      &::before {
        content: "";
        background: linear-gradient(to bottom, #cacaca 100%, rgba(255, 255, 255, 0) 0%) repeat-y left;
        width: 1px;
        height: 12px;
        margin: auto 0;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        z-index: 0;
      }
      &:last-child {
        &::after {
          content: "";
          background: linear-gradient(to bottom, #cacaca 100%, rgba(255, 255, 255, 0) 0%) repeat-y left;
          width: 1px;
          height: 12px;
          margin: auto 0;
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          z-index: 0;
        }
      }
    }
    a {
      color: inherit;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__copy {
    color: #fff;
    margin-top: 30px;
    @include font-line(11, 11);
    text-align: center;
  }
}
