/**/
.l-fixed {
  width: 55px;
  position: fixed;
  top: 300px;
  right: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  li{
    list-style: none;
    height: 160px;
    width: 55px;
    margin-bottom: 0;
    a {
      display: block;
      width: 100%;
      height: 100%;
      transition: all 0.4s ease;
      text-decoration: none;
    }
    .text {
      @include font-line(15, 49);
      font-weight: 700;
      letter-spacing: 0.08em;
      writing-mode: vertical-rl;
    }
    .icon {
      @include font-line(25, 25);
      font-weight: 500;
      margin-top: 7px;
      text-align: center;
      display: block;
    }
  }

  &__register {
    background: $color-sub;
    border: 3px solid $color-sub;
    color: #fff;
    &:hover {
      @include opacity(0.7);
    }
    .text {
      padding-top: 23px;
    }
  }
  &__contact {
    background: $color-sub;
    border: 3px solid $color-sub;
    color: #fff;
    &:hover {
      @include opacity(0.7);
    }
    .text {
      padding-top: 10px;
    }
  }
  &__login {
    background: $color-accent;
    border: 3px solid $color-accent;
    color: #fff;
    &:hover {
      @include opacity(0.7);
    }
    .text {
      padding-top: 23px;
    }
  }
  &__favorite {
    background: $color-sub;
    border: 3px solid $color-sub;
    color: #fff;
    &:hover {
      @include opacity(0.7);
    }
    .text {
      padding-top: 18px;
    }
  }
  &__reserve {
    background: $color-main;
    border: 3px solid $color-main;
    color: #fff;
    &:hover {
      @include opacity(0.7);
    }
    .text {
      padding-top: 23px;
    }
  }
}
