.c-checkbox {
  $checkbox: &;
  display: inline-block;
  &__input {
    width: 0 !important;
    height: 0 !important;
    margin: 0 !important;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    visibility: hidden;
    display: inline-block;
    &:checked + #{$checkbox}__parts {
      &::after {
        @include font-line(17);
        content: $if-check;
        color: #333;
        font: normal normal normal 17px/1 "myiconfont";
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        position: absolute;
        top: -1px;
        left: 0;
        margin: auto;
        z-index: 1;
      }
    }
  }
  &__parts {
    padding-left: 25px;
    position: relative;
    @include font-line(14, 14);
    display: inline-block;
    &::before {
      background: #fff;
      border: 1px solid #333;
      content: "";
      display: block;
      position: absolute;
      top: -2px;
      left: 0;
      height: 18px;
      width: 18px;
    }
  }
}

.c-radiobox {
  $radiobox: &;
  height: 15px;
  display: inline-block;
  &__input {
    width: 0 !important;
    height: 0 !important;
    margin: 0 !important;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    visibility: hidden;
    display: inline-block;
    &:checked + #{$radiobox}__parts {
      &::before {
        opacity: 1;
      }
    }
  }
  &__parts {
    height: 15px;
    padding: 0 0 0 25px;
    @include font-line(14, 14);
    display: inline-block;
    position: relative;
    cursor: pointer;
    &::before,
    &::after {
      content: "";
      margin: auto;
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
    }
    // 
    &::before {
      background-color: #666;
      border-radius: 50%;
      width: 7px;
      height: 7px;
      left: 4px;
      opacity: 0;
      z-index: 2;
    }
    // 
    &::after {
      background-color: #ececec;
      border: 1px solid #838383;
      border-radius: 50%;
      width: 15px;
      height: 15px;
      left: 0;
      z-index: 1;
    }
  }
}

.c-selectbox {
  background: #fff url(../img/bg_select.png) no-repeat top right;
  background-size: 26px 38px;
  border: 1px solid #838383;
  border-radius: 3px;
  height: 37px;
  padding: 0 20px 0 10px;
  font-weight: 400;
  @include font-line(14, 35);
  vertical-align: top;
  appearance: none;
  cursor: pointer;
  &::-ms-expand {
    display: none;
  }
}

.c-textbox {
  background-color: #fff;
  border: 1px solid #838383;
  border-radius: 3px;
  width: 100%;
  height: 37px;
  padding-left: 10px;
}

.c-textarea {
  background-color: #fff;
  border: 1px solid #838383;
  border-radius: 3px;
  width: 100%;
  margin: 0;
  padding: 18px;
  @include font-line(14, 30);
}
