.c-seal01 {
  background-color: $color-main;
  margin: 0 10px 0 0;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;

  &:last-child {
    margin-right: 0;
  }

  &--l {
    width: 110px;
    height: 30px;
    @include font-line(16, 16);
  }
  &--m1 {
    width: 70px;
    height: 26px;
    @include font-line(14, 14);
  }
  &--m2 {
    width: 110px;
    height: 26px;
    @include font-line(14, 14);
  }
  &--s1 {
    width: 60px;
    height: 20px;
    @include font-line(12, 12);
  }
  &--s2 {
    width: 50px;
    height: 26px;
    @include font-line(12, 12);
  }
}

.c-seal02 {
  background-color: #fff;
  border: 1px solid $color-text01;
  margin: 0 10px 0 0;
  color: $color-text01;
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;

  &:last-child {
    margin-right: 0;
  }

  &--l {
    width: 110px;
    height: 30px;
    @include font-line(16, 16);
  }
  &--m {
    width: 90px;
    height: 26px;
    @include font-line(12, 12);
  }
  &--s {
    width: 60px;
    height: 20px;
    @include font-line(12, 12);
  }
}
