.l-header {
  $header: '.l-header';
  position: relative;
  &__top {
    height: 120px;
    background: #fff;
  }
  &__inner {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 1;
  }

  &__left {
    flex: 0 0 100px;
    position: relative;
  }
  &__center {
    flex: auto;
  }
  &__description {
    margin: 0;
    padding: 0;
    font-weight: 600;
    @include font-line(15, 15);
    color: #484848;
  }
  &__logo {
    margin: 0 0;
    padding: 0;
    a {
      border: none;
      padding: 0;
      text-decoration: none;
      &:hover {
        opacity: 0.6;
      }
    }
    img {
      width: 95px;
      height: 95px;
    }
  }
  &__balloon {
    width: 170px;
    height: 59px;
    border: 1px solid $color-sub;
    border-radius: 10px;
    background: #fffaf3;
    margin: 13px 0 0 15px;
    padding: 0 15px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    &::before ,
    &::after {
      content: "";
      position: absolute;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      bottom: 10px;
    }
    &::before {
      border-right: 13px solid $color-sub;
      left: -11px;
    }
    &::after {
      border-right: 13px solid #fffaf3;
      left: -9px;
    }
    .text {
      font-weight: 500;
      @include font-line(13, 22);
      display: block;
      position: relative;
      strong {
        color: $color-sub;
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        @include font-line(35, 35);
        letter-spacing: -0.01em;
      }
    }
  }

  &__right {
    flex: 0 0 490px;
  }
  &__buttons {
    height: 30px;
    display: flex;
    justify-content: flex-end;
    position: relative;
    .text {
      height: 30px;
      @include font-line(13, 13);
      color: $color-text02;
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    #{$header}__button {
      width: 130px;
      height: 30px;
      font-weight: 700;
      @include font-line(13, 13);
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 15px;
      &:hover {
        @include opacity(0.5);
      }
      &.button01 {
        background-color: $color-accent;
        color: #fff;
      }
      &.button02 {
        background-color: $color-sub;
        color: #fff;
        margin-left: 10px;
      }
    }
  }
  &__tel {
    text-align: right;
    margin-top: 10px;
    &__top {
      width: 375px;
      height: 40px;
      margin: 0 0 0 auto;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .icon {
        margin-right: 5px;
        @include font-line(28, 28);
        display: block;
      }
      .tel {
        height: 36px;
        font-weight: 700;
        font-family: 'Roboto', sans-serif;
        @include font-line(30, 30);
        letter-spacing: -0.02em;
        display: flex;
        align-items: center;
      }
      .snsBtn {
        border-radius: 50%;
        height: 30px;
        width: 30px;
        margin-right: 5px;
        @include font-line(18, 18);
        color: #fffbf1;
        text-align: center;
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        transition: opacity 0.4s ease-out;
        top: -1px;
        &:hover {
          opacity: 0.6;
        }
        &:nth-child(2) {
          margin-right: 10px;
        }
        &__insta {
          background-color: #875024;
        }
        &__fb {
          background-color: #2f3c75;
        }
      }
    }
    .ruby {
      @include font-line(13, 13);
      text-align: right;
    }
  }
}

.l-gNavi {
  $gnavi: &;
  background-color: #cdead1;
  height: 80px;
  position: relative;
  z-index: 1;
  .l-gNavi__inner {
    height: 100%;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__item {
    flex: 0 0 14.285%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: -5px;

    @mixin gnavi-item-current {
      .inner {
        position: relative;
        &:before {
          content: '';
          border: 3px solid $color-primary;
          border-radius: 3px;
          margin: 0 auto;
          width: 71px;
          height: 0;
          position: absolute;
          right: 0;
          left: 0;
          bottom: 9px;
        }
        .en,.ja {
        }
      }
    }
    &.is-current,
    &:hover {
      @include gnavi-item-current;
    }
    @at-root .page-home {
      #gnavi-home {
        @include gnavi-item-current;
      }
    }
    @at-root .page-estate {
      #gnavi-estate {
        @include gnavi-item-current;
      }
    }
    @at-root .page-school {
      #gnavi-school {
        @include gnavi-item-current;
      }
    }
    @at-root .page-buy {
      #gnavi-buy {
        @include gnavi-item-current;
      }
    }
    @at-root .page-sell {
      #gnavi-sell {
        @include gnavi-item-current;
      }
    }
    @at-root .page-reform {
      #gnavi-reform {
        @include gnavi-item-current;
      }
    }
    @at-root .page-event {
      #gnavi-event {
        @include gnavi-item-current;
      }
    }
    @at-root .page-company {
      #gnavi-company {
        @include gnavi-item-current;
      }
    }

  }
  &__link {
    width: 100%;
    height: 100%;
    color: $color-text01;
    text-decoration: none;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    text-align: center;
    .icon-home {
      @include font-line(16, 16);
      color: $color-sub;
    }
    .ja {
      margin: 0;
      font-weight: 900;
      @include font-line(12, 12);
      display: block;
    }
    .en {
      margin: 0 0 6px;
      font-weight: 900;
      font-family: "M PLUS Rounded 1c";
      @include font-line(17, 17);
      letter-spacing: 0.02em;
      display: block;
    }
  }
}

