.c-card {
  background-color: #fff;
  border: 1px solid #a0a0a0;
  flex: 0 0 310px;
  text-align: center;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  a {
    border: none;
    margin: 0;
    padding: 0;
    text-decoration: none;
    &:hover {
      opacity: 0.6;
    }
  }
  &__wrap {
    display: block;
    color: $color-text01;
    text-decoration: none;
  }
  &__photo {
    width: 100%;
    height: 232px;
    border-bottom: 1px solid #a0a0a0;
    position: relative;
    img {
      height: 100%;
      width: 100%;
      object-fit: contain;
      font-family: 'object-fit: contain;';
    }
  }
  &__ribbon {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    @include font-line(16, 18);
    font-family: 'Roboto',sans-serif;
    font-weight: 900;
    color: #fff;
    background: $color-main;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 2;
    &--new {
    }
    &--pricedown {
      @include font-line(14, 18);
    }
    &--sold,
    &--nego {
      width: 116px;
      height: 116px;
      background-color: #000;
      @include opacity(0.6);
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }

  &__body {
    padding: 8px 16px;
    height: 146px;
  }
  &__type {
    background: #494440;
    width: 90px;
    height: 30px;
    font-weight: 500;
    @include font-line(14, 14);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 1;
  }
  &__price {
    font-weight: 700;
    @include font-line(40, 40);
    color: #ff6b31;
    .sub {
      @include font-line(13, 13);
      color: $color-text01;
    }
  }

  &__info {
    width: 100%;
    margin: 0;
    color: $color-text01;
    text-align: center;
  }
  &__infoBottom {
    border-top: 1px solid #a0a0a0;
    text-align: center;
  }
  &__date {
    @include font-line(11, 28);
  }
  &__addr {
    font-weight: 700;
    @include font-line(18, 30);
    height: 60px;
  }
  &__size {
    @include font-line(18, 30);
  }
}

