.c-heading {
  // 01
  // : <h2 class="c-heading01"><span><span class="c-heading01__en">MIDASHI</span><span class="c-heading01__sub"></span></span></h2>
  &01 {
    margin: 0 auto 38px;
    padding: 0 0 20px 0;
    font-weight: 500;
    @include font-line(30, 30);
    font-family: "M PLUS Rounded 1c";
    color: $color-text01;
    letter-spacing: 0.02em;
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
    &:before,
    &:after {
      content: '';
      flex-grow: 1;
      height: 5px;
      background: #000;
      display: block;
    }
    &:before {
      margin-right: 20px;
    }
    &:after {
      margin-left: 20px;
    }
    &__en {
      border-bottom: 8px solid $color-sub;
      position: relative;
      display: inline-block;
      line-height: 0.5;
      font-weight: 700;
      white-space: nowrap;
    }
    &__sub {
      margin: 15px 0 0 0;
      padding: 0;
      font-family: "Noto Sans Japanese", sans-serif;
      font-weight: 700;
      letter-spacing: 0;
      text-align: center;
      display: block;
    }
  }

  // 01
  // : <h3 class="c-heading02">01</h3>
  &02 {
    border-bottom: 4px solid #494440;
    margin: 0 auto 40px;
    padding: 0 0 20px;
    font-weight: 700;
    @include font-line(25, 25);
    color: $color-text01;
    text-align: center;
    display: block;
  }

  // 02
  // : <h3 class="c-heading03">02</h3>
  &03 {
    border-bottom: 4px solid #494440;
    margin: 0 auto 40px;
    padding: 0 0 20px;
    font-weight: 700;
    @include font-line(25, 25);
    color: $color-text01;
    text-align: left;
    display: block;
  }

  // 03
  // : <h3 class="c-heading04">03</h3>
  &04 {
    border-bottom: 1px solid #bababa;
    height: 30px;
    margin: 0 0 35px;
    padding: 0 0 35px 15px;
    font-weight: 700;
    @include font-line(17, 30);
    color: $color-text01;
    text-align: left;
    position: relative;
    &::before {
      content: '';
      border-left: 7px solid $color-primary;
      height: 30px;
      position: absolute;
      left: 0;
    }
  }

  // 01
  // : <h4 class="c-heading07">01</h4>
  &05 {
    background-color: #b2e2f8;
    height: 35px;
    margin: 0 0 35px;
    padding: 0 0 0 10px;
    font-weight: 700;
    @include font-line(17, 35);
    color: $color-text01;
    text-align: left;
  }
}
