.p-information {
  $information: &;
  background: #fff;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  position: relative;

  .totop {
    width: 70px;
    height: 70px;
    display: block;
    position: absolute;
    background-color: $color-main;
    color: #fff;
    border-radius: 35px;
    right: -150px;
    top: -30px;
    z-index: 1;
    @include font-line(20, 70);
    text-align: center;
    &:hover {
      @include opacity(0.7);
    }
  }

  &__inner {
    width: $wrap-width;
    margin: 0 auto;
    padding: 59px 0 40px;
    position: relative;
  }

  &__description {
    margin: 0 0 14px;
    @include font-line(16, 16);
    text-align: center;
  }

  &__top {
    display: flex;
    justify-content: space-between;

    #{$information}__left {
      flex: 0 0 201px;
      height: 100%;
      .logo {
        &:hover {
          opacity: 0.6;
        }
        img {
          width: 201px;
          height: 199px;
        }
      }
    }

    #{$information}__center {
      flex: 0 0 427px;
      height: 100%;
      display: flex;
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
      .textTop {
        margin: 0 0 4px;
        @include font-line(14, 14);
      }
      .textBottom {
        @include font-line(15, 15);
      }
      .tel {
        height: 50px;
        margin: 0;
        letter-spacing: -0.02em;
        @include font-line(50, 50);
        font-family: 'Roboto',sans-serif;
        font-weight: 900;
        display: flex;
        align-items: center;
        i {
          margin-right: 10px;
          @include font-line(44);
        }
      }
      .buttons {
        width: 400px;
        margin-top: 15px;
        .button {
          margin-top: 18px;
          height: 50px;
          border-radius: 25px;
          @include font-line(18, 18);
        }
      }
    }

    #{$information}__right {
      flex: 0 0 280px;
      position: relative;
      .logoImage {
        border: 1px solid #9d9d9d;
        border-radius: 10px;
        width: 280px;
        height: 235px;
      }
      .address {
        width: 280px;
        height: 73px;
        @include font-line(14, 24);
        color: #fff;
        font-weight: 500;
        background-color: #111;
        @include opacity(0.74);
        padding: 12px 29px;
        border-radius: 0 0 10px 10px;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
  }
}
