.p-quickSearch {
  border: 3px solid $color-accent;
  background: #fff;
  padding: 36px 0 44px;
  &__heading {
    margin: 0;
    &:before,
    &:after {
      height: 0;
    }
    &__icon {
      margin-left: 40px;
      &::before {
        content: '';
        background: url(../img/icon_home_quicksearch.png) no-repeat;
        width: 34px;
        height: 37px;
        position: absolute;
        left: -40px;
        bottom: -8px;
      }
    }
    &__sub {
      @include font-line(20, 20);
    }
  }
  &__inner {
    padding: 16px 20px;
    background-color: #cdead1;
    border-radius: 6px;
    border: 3px solid $color-main;
  }
  .inputBlock {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    margin: 0 auto;
    position: relative;
    &__box {
      .title {
        @include font-line(16, 40);
        font-weight: 800;
        letter-spacing: 0.1em;
        text-align: center;
        margin: 0;
        .separate_bt{
          margin: 0 8px 0 20px;
        }
      }
    }
    .type {
      flex: 0 0 271px;
      position: relative;
      .checkList {
        font-size: 0;
        background: #FFF;
        border: 1px solid #eaeaea;
        border-radius: 5px;
        color: black;
        width: 271px;
        padding: 19px 16px 0;
        margin: 0 auto;
        &__item {
          @include font-line(14, 16);
          font-weight: 500;
          display: inline-block;
          margin-bottom: 19px;
          letter-spacing: 0.06em;
          width: 50%;
        }
      }
    }
    .priceArea {
      flex: 0 0 481px;
      padding: 9px 0;
      .inputBlock__box {
        height: 62px;
      }
      .price,
      .area,
      .station {
        display: flex;
        align-items: center;
        .title {
          flex: 0 0 61px;
          text-align: right;
          padding-right: 7px;
        }
      }
      .price {
        .inputBox {
          display: flex;
          align-items: center;
          flex: 1 1 auto;
          justify-content: space-between;
          &__select {
            border: 1px solid #eaeaea;
            border-radius: 5px;
            width: 180px;
            height: 40px;
          }
          .separate {
          }
        }
      }
      .areaStation {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .area,
      .station {
        flex: 0 0 50%;
        .inputBox {
          &__select {
            border: 1px solid #eaeaea;
            border-radius: 5px;
            width: 180px;
            height: 40px;
          }
        }
      }
    }
  }
  .buttonBlock{
    .button {
      width: 143px;
      height: 143px;
      border-radius: 72px;
      flex-direction: column;
      @include font-line(14, 21);
      font-weight: 700;
      strong {
        @include font-line(25, 37);
      }
    }
  }

  &.quick-search01 {
  }
  &.quick-search02 {
  }
}
