.c-read01 {
  font-weight: 700;
  @include font-line(30, 35);
}

.c-read02 {
  font-weight: 700;
  @include font-line(23, 28);
}

.c-read03 {
  font-weight: 700;
  @include font-line(20, 25);
}

.c-text01 {
  font-weight: 500;
  @include font-line(16, 30);
  strong, em {
    background-color: #fff996;
    font-weight: 700;
    font-style: normal;
  }
}

.c-annotation {
  margin: 0 0 30px;
  padding: 0 0 0 30px;
  @include font-line(13, 16);
  position: relative;
  &__number {
    @include font-line(13, 16);
    position: absolute;
    top: 0;
    left: 0;
    &::before {
      content: "";
    }
  }
}
