.c-button01 {
  $button01: &;
  background-color: #fff;
  border-width: 1px;
  border-style: solid;
  margin: 0 auto;
  font-weight: 700;
  @include font-line(20, 20);
  color: #fff;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;

  &--xl {
    border-radius: 45px;
    height: 90px;
    @include font-line(20, 20);
  }
  &--l {
    border-radius: 30px;
    height: 60px;
    @include font-line(20, 20);
  }
  &--m {
    border-radius: 20px;
    height: 40px;
    @include font-line(16, 16);
  }
  &--s {
    border-radius: 20px;
    height: 40px;
    @include font-line(14, 14);
  }

  &--main {
    background-color: $color-main;
    border-color: $color-main;
    color: #fff;
    &--w {
      background-color: #fff;
      border-color: $color-main;
      color: $color-text01;
    }
  }

  &--sub {
    background-color: $color-sub;
    border-color: $color-sub;
    color: #fff;
    &--w {
      background-color: #fff;
      border-color: $color-sub;
      color: $color-text01;
    }
  }

  &--accent {
    background-color: $color-accent;
    border-color: $color-accent;
    color: #fff;
    &--w {
      background-color: #fff;
      border-color: $color-accent;
      color: $color-text01;
    }
  }

  &--bullet {
    &::after {
      @include font-line(14, 14);
      content: "\f105";
      font-family: "Font Awesome 5 free";
      font-weight: 700;
      position: relative;
      display: inline-block;
      margin: 0 0 0 10px;
    }
  }

  &:hover {
    @include opacity(0.7);
  }
  &:focus {
    outline: none;
  }

  &--reverse {
    &#{$button01}--main--w {
      &:hover {
        @include opacity(1);
        background-color: $color-main;
        color: #fff;
      }
    }
    &#{$button01}--sub--w {
      &:hover {
        @include opacity(1);
        background-color: $color-sub;
        color: #fff;
      }
    }
    &#{$button01}--accent--w {
      &:hover {
        @include opacity(1);
        background-color: $color-accent;
        color: #fff;
      }
    }
  }
}

.c-button02 {
  $button02: &;
  background-color: #fff;
  border: 1px solid $color-accent;
  margin: 0 auto;
  font-weight: 700;
  color: $color-text01;
  box-shadow: 2.179px 3.355px 2.64px 1.36px #f8b52b;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;

  &--xl {
    height: 60px;
    border-radius: 30px;
    @include font-line(28, 28);
  }
  &--l {
    border-radius: 30px;
    height: 60px;
    @include font-line(20, 20);
  }
  &--m {
    border-radius: 26px;
    height: 52px;
    @include font-line(18, 18);
  }
  &--s {
    border-radius: 20px;
    height: 40px;
    @include font-line(13, 13);
  }

  &--bullet {
    &::after {
      @include font-line(14, 14);
      content: "\f105";
      font-family: "Font Awesome 5 free";
      font-weight: 700;
      position: relative;
      display: inline-block;
      margin: 0 0 0 10px;
      top: 1px;
    }
  }

  &:hover {
    background: $color-sub;
    border-color: $color-sub;
    color: #fff;
  }
  &:focus {
    outline: none;
  }
}
