.searchArea {
  width: 860px;
  margin: 0 auto;
  &__title {
    @include font-line(18);
    border-bottom: 4px solid $color-accent;
    padding: 10px;
    font-weight: bold;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    .button {
      height: 25px;
      line-height: 20px;
    }
  }
  .buttonBlock {
    text-align: center;
    margin: 30px 0;
    .button {
      width: 360px;
      margin: 0 auto;
    }
  }
  .areaList {
    @include grid(3, 20px);
  }
  .checkList {
    @include grid(5, 20px);
  }
  .tb_search {
    border-collapse: collapse;
    border-bottom: 1px solid #ccc;
    margin: 0 auto;
    width: 100%;
    th {
      border-top: 1px solid #ccc;
      padding: 15px;
    }
    td {
      border-top: 1px solid #ccc;
      padding: 15px;
      &.estatetabletit3 label {
        display: inline-block;
        width: 159px;
      }
    }
    th {
      border-right: 1px dotted #ccc;
    }
    label {
      cursor: pointer;
      margin-right: 0.8em;
    }
    input[type="text"] {
      width: 180px;
      margin: 0 0.5em;
    }
    select {
      width: 180px;
      margin: 0 0.5em;
    }
    textarea {
      border: 1px #ccc solid;
    }
    input {
      &[type=checkbox], &[type=radio] {
        width: 18px;
        height: 18px;
        vertical-align: middle;
      }
    }
  }

  .gakkuSearch {
    .gakkuBox {
      padding: 20px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      display: block;
      overflow: hidden;
      &__title {
        @include font-line(16, 20);
        font-weight: normal;
        margin-top: 0;
      }
    }
  }
}
