// 
// <blockquote class="c-blockquote01">
//     
//     <span class="c-blockquote01--src"></span>
// </blockquote>
.c-blockquote01 {
  border: 3px solid #dcdcdc;
  border-radius: 8px;
  margin: 0;
  padding: 30px 95px 80px 80px;
  font-style: italic;
  @include font-line(16, 24);
  color: #818181;
  position: relative;
  &__src {
    font-style: italic;
    @include font-line(16, 16);
    text-align: right;
    position: absolute;
    right: 40px;
    bottom: 30px;
    &::before {
      content: "";
    }
  }
  &::before {
    content: "";
    font-style: normal;
    @include font-line(72, 85);
    color: #fff;
    display: inline-block;
    position: absolute;
    top: 30px;
    left: 15px;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: #dcdcdc;
    text-align: center;
  }
}

// 
// <blockquote class="c-blockquote02">
//     
//     <span class="c-blockquote02--src"></span>
// </blockquote>
.c-blockquote02 {
  background-color: #fff;
  border-left: 8px solid #bfbfbf;
  margin: 0;
  padding: 5px 150px 35px 25px;
  font-style: italic;
  @include font-line(16, 24);
  color: $color-text01;
  position: relative;
  &__src {
    font-style: italic;
    @include font-line(16, 16);
    text-align: right;
    color: $color-text01;
    position: absolute;
    right: 0;
    bottom: 0;
    &::before {
      content: "";
    }
  }
  &::before {
    content: "";
    font-style: normal;
    @include font-line(100, 100);
    color: #bfbfbf;
    display: inline-block;
    position: absolute;
    top: 7px;
    right: 0;
  }
}
