.page-home {
  background: #fffaf3;
  .kv {
    background: url('../img/bg_home_kv.jpg') no-repeat center top;
    //width: $body-min-width;
    height: 390px;
    margin: 50px auto;
    padding: 0;
    position: relative;
    &__inner {
      background: rgba(255, 255, 255, .6);
      width: 298px;
      height: 288px;
      margin: auto;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      text-align: center;
      padding-top: 25px;
      .text {
        font-family: "M PLUS Rounded 1c";
        font-weight: 700;
        @include font-line(28, 42);
        color: $color-main;
        &__upper {
          text-align: left;
          display: block;
          padding-left: 20px;
        }
        &__lower {
          text-align: right;
          display: block;
          padding-right: 20px;
        }
      }
      img {
        margin-top: 10px;
        width: 143px;
        height: 142px;
      }
    }
    &__balloon {
      background: $color-sub;
      width: 130px;
      height: 130px;
      position: absolute;
      border-radius: 50%;
      top: 250px;
      left: 425px;
      transform: rotate(-6deg);
      -webkit-transform: rotate(-6deg);
      &:before,
      &:after {
        content: '';
        border-bottom: 5px solid $color-primary;
        border-radius: 3px;
        width: 20px;
        position: absolute;
        transform: rotate(68deg);
        -webkit-transform: rotate(68deg);
      }
      &:before {
        left: 27px;
        top: 3px;
      }
      &:after {
        left: 17px;
        top: 7px;
      }
      .text {
        font-family: "M PLUS Rounded 1c";
        @include font-line(20, 35);
        text-align: center;
        color: #fff;
        padding: 18px 0 0;
        &--big {
          @include font-line(24, 0);
          position: relative;
          top: 4px;
          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
          }
        }
        strong {
          @include font-line(24, 24);
          font-weight: 700;
        }
      }
    }
  }
  .quo {
    margin:0 auto 50px;
    text-align: center;
    a {
      border-bottom: 0;
    }
  }
  .introduction {
    padding: 50px 0 0;
    height: 470px;
    &__heading {
      width: 100%;
      font-weight: 700;
      @include font-line(40, 40);
      font-family: "M PLUS Rounded 1c";
      text-align: center;
      letter-spacing: 0.06em;
    }
    &__inner {
      display: flex;
      justify-content: space-between;
      position: relative;
      margin: 0 auto;
      padding: 0;
      z-index: 0;
    }
    &__left {
      flex: 0 0 437px;
      height: 271px;
      position: relative;
      border: 2px solid $color-main;
      border-radius: 10px;
      background: #fff;
      padding: 10px 30px;
      .text01 {
        @include font-line(18, 32);
        font-weight: 700;
        background: $color-main;
        border-radius: 16px;
        text-align: center;
        margin: 15px 20px;
        color: #fff;
      }
      .text02 {
        margin: 0;
        @include font-line(12, 20);
        text-align: left;
      }
      .date {
        padding-top: 2px;
        font-family: 'Roboto', sans-serif;
        @include font-line(18, 18);
        text-align: center;
        margin: 8px 0;
      }
    }
    &__right {
      width: 585px;
      text-align: center;
      position: absolute;
      right: 0;
      .sum {
        width: 100%;
        display: flex;
        justify-content: space-between;
        .boardCommon {
          flex: 0 0 175px;
          position: relative;
          .inside {
            background-color: $color-sub;
            border-radius: 83px;
            width: 100%;
            height: 165px;
            font-weight: 700;
            color: #fff;
            display: flex;
            flex-flow: column wrap;
            justify-content: center;
            align-items: center;
            position: relative;
            .type {
              margin: 10px auto 5px;
              @include font-line(17, 17);
            }
            .number {
              @include font-line(60, 60);
              font-family: 'Roboto', sans-serif;
              display: flex;
              justify-content: center;
              align-items: baseline;
              padding-left: 23px;
              .sub {
                margin-left: 5px;
                @include font-line(17, 17);
              }
            }
            .state {
              margin: 5px auto 0;
              @include font-line(17, 17);
            }
            &__member {
              background: $color-main;
              margin-top: 37px;
              position: relative;
              &:before {
                content: '';
                border: 9px solid $color-sub;
                border-radius: 9px;
                width: 149px;
                position: absolute;
                top: -10px;
                left: 0;
                right: 0;
                margin: 0 auto;
              }
              &:after {
                content: '';
                border: 15px solid $color-sub;
                border-radius: 15px;
                width: 0;
                height: 45px;
                position: absolute;
                top: -37px;
                left: 0;
                right: 0;
                margin: 0 auto;
              }
            }
            &__general,
            &__store {
              margin-top: 57px;
            }
          }
          .outside {
            width: 100%;
            padding: 10px;
            margin: 0 auto;
            left: 0;
            right: 0;
            .notice {
              margin: 0;
              color: #41210f;
              font-weight: 700;
              @include font-line(12, 14);
              display: block;
            }
          }
        }
        .center {
          flex: 0 0 215px;
        }
      }
    }
    .buttonBlock {
      margin: 48px auto 0;
      .button {
        width: 700px;
        @include font-line(28, 28);
      }
    }
  }

  .information {
    margin: 20px auto;
    .box {
      width: 100%;
      height: 53px;
      margin: auto;
      padding: 0;
      display: flex;
    }
    &__heading {
      background-color: #41210f;
      border: 1px solid $color-accent;
      flex: 0 0 213px;
      height: 100%;
      margin: 0;
      padding: 0;
      font-family: "M PLUS Rounded 1c";
      font-weight: 700;
      @include font-line(18, 18);
      color: #fff;
      border-radius: 10px 0 0 10px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        margin-right: 10px;
      }
    }
    &__text {
      background: #fff;
      border: 1px solid $color-accent;
      flex: 0 0 787px;
      height: 100%;
      margin: 0;
      padding:0;
      @include font-line(16, 16);
      border-radius: 0 10px 10px 0;
      display: flex;
      align-items: center;
      position: relative;
      overflow: hidden;
      white-space: nowrap;
    }
  }

  .newsEvent {
    .contentBox {
      display: flex;
      justify-content: space-between;
      padding: 80px 0 67px;
    }
    &__heading {
      &:before,
      &:after {
        display: none;
      }
      &__sub {
        display: inline-block;
        margin: 0 0 0 40px;
        position: relative;
        font-family: "M PLUS Rounded 1c";
        &:before {
          content: '';
          border-left: 3px solid ;
          height: 24px;
          position: absolute;
          top: 0;
          bottom: 0;
          left: -20px;
          margin: auto;
        }
      }
    }
    .news {
      flex: 0 0 470px;
      &__heading {
        &__icon {
          margin-left: 40px;
          &::before {
            content: '';
            background: url(../img/icon_home_news.png) no-repeat;
            width: 32px;
            height: 53px;
            position: absolute;
            left: -40px;
            bottom: -10px;
          }
        }
      }
      &List {
        width: 100%;
        height: 290px;
        overflow-y: auto;
        &::-webkit-scrollbar{
          width: 15px;
        }
        &::-webkit-scrollbar-track{
          background: #eaeaea;
          border: none;
          box-shadow: none;
        }
        &::-webkit-scrollbar-thumb{
          background: #494440;
          box-shadow: none;
        }
        &::-webkit-scrollbar-button {
          height: 28px;
          background: #eaeaea no-repeat center;
          &:vertical:decrement {
            background-image: url("../img/bg_home_news_arrow_up.png");
            border-radius: 7px 7px 0 0;
          }
          &:vertical:increment {
            background-image: url("../img/bg_home_news_arrow_down.png");
            border-radius: 0 0 7px 7px;
          }
        }
        &__item {
          border-bottom: 1px dotted #a0a0a0;
          margin: 0 18px 0 0;
          padding: 15px 0;
          display: flex;
          &:first-child {
            padding-top: 0;
          }
        }
        &__date {
          font-family: "Arial", sans-serif;
          font-weight: 400;
          @include font-line(15, 24);
          letter-spacing: 0.01em;
        }
        &__text {
          margin-left: 40px;
          @include font-line(15, 24);
          letter-spacing: 0.08em;
        }
      }
    }

    .event {
      flex: 0 0 470px;
      &__heading {
        &__icon {
          margin-left: 50px;
          &::before {
            content: '';
            background: url(../img/icon_home_event.png) no-repeat;
            width: 37px;
            height: 37px;
            position: absolute;
            left: -50px;
            bottom: -10px;
          }
        }
      }
      &__inner {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
      }
      &__detail{
        width: 32%;
      }
      &__photo {
        height: 170px;
        margin: 0 auto 15px;
        .photo {
          width: 100%;
          height: 100%;
          object-fit: cover;
          font-family: 'object-fit: cover;';
          border-radius: 10px;
        }
      }
      &__body {
        @include font-line(14, 20);
        margin-bottom: 15px;
      }
      &__date {
        @include font-line(14, 24);
        margin: 0;
      }
      &__title {
        @include font-line(16, 24);
        margin: 0;
      }
      &__bottom{
        margin: auto;
        width: 150px;
        font-family: 'Roboto', sans-serif;
      }
    }
  }

  .search {
    padding: 80px 0;
    &__heading {
      &__icon {
        margin-left: 40px;
        &::before {
          content: '';
          background: url(../img/icon_home_search.png) no-repeat;
          width: 30px;
          height: 38px;
          position: absolute;
          left: -40px;
          bottom: -10px;
        }
      }
    }
    &__tabs {
      display: flex;
      margin-top: 45px;
      button {
        @include font-line(18, 49);
        color: $color-text01;
        cursor: pointer;
        font-weight: 700;
        width: 230px;
        height: 65px;
        padding: 0;
        background-color: #fff;
        border: 3px solid $color-main;
        border-bottom: none;
        margin-left: 20px;
        border-radius: 10px 10px 0 0;
        &.is-active,
        &:hover {
          background-color: $color-main;
          color: #fff;
        }
        &:focus {
          outline: none;
        }
        em {
          @include font-line(30, 49);
          font-style: normal;
        }
        .fas {
          margin-left: 7px;
        }
      }
    }
    .tabContent {
      background-color: #fff;
      border-radius: 20px;
      display: none;
      height: 780px;
      overflow-y: auto;
      position: relative;
      border: 3px solid $color-main;
      &.is-active {
        display: block;
      }
    }

    &__map {
      background: url(../img/bg_home_search_map.png) no-repeat left top;
      height: 531px;
      padding: 20px 30px;
      position: relative;
      .mask,
      .landmark {
        background-repeat: no-repeat;
        background-position: top left;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
      .mask {
        z-index: 0;
      }
      .landmark {
        background-image: url(../img/bg_home_search_map_landmark.png);
        z-index: 1;
      }
      .flow {
        display: flex;
        height: 45px;
        width: 449px;
        margin:0;
        padding: 0 15px;
        border: 2px solid #fff;
        border-radius: 10px;
        @include font-line(16, 43);
        background: #fff;
        justify-content: space-between;
        text-align: center;
        position: absolute;
        z-index: 2;
        box-shadow: 0 0 20px 0 $color-accent;
        &__step {
          position: relative;
        }
        .number {
          height: 23px;
          width: 23px;
          border-radius: 50%;
          background-color: $color-text01;
          color: #fff;
          line-height: 1.3;
          margin-top: 10px;
        }
      }
      .map_menu {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: baseline;
        position: absolute;
        top: 84px;
        width: 130px;
        z-index: 2;
        &__title {
          @include font-line(16, 16);
          font-weight: 700;
          text-align: center;
          width: 130px;
          margin-bottom: 10px;
        }
        .btn {
          @include font-line(16, 31);
          font-weight: 500;
          cursor: pointer;
          letter-spacing: 0.05em;
          margin-bottom: 10px;
          position: relative;
          text-align: center;
          min-width: 130px;
          border: 1px solid $color-text02;
          border-radius: 5px;
          background-color: #fff;
          &:hover,
          &.is-active {
            background-color: $color-text02;
            color: #fff;
          }
        }
      }
      .bnrBlock {
        width: 210px;
        position: absolute;
        bottom: 30px;
        left: 30px;
        z-index: 2;
        border-radius: 10px;
        background-color: #fff;
        .bnr {
          display: block;
          padding-top: 13px;
          text-align: center;
          text-decoration: none;
          .btn {
            width: 180px;
            margin: auto;
          }
          .fas {
            @include font-line(12);
            margin-right: 5px;
          }
          &.register {
            .text {
              @include font-line(16, 24);
              font-weight: 700;
              margin: 0 0 10px;
              em {
                font-style: normal;
                color: #f46669;
              }
              .if {
                padding: 0 2px;
              }
            }
          }
          &.login {
            margin-top: 16px;
            border-top: 1px dashed $color-text01;
            padding-bottom: 16px;
            .text {
              @include font-line(14, 21);
              font-weight: 700;
              margin: 0 20px 10px;
              text-align: left;
            }
            .pass_forget {
              @include font-line(11, 22);
              letter-spacing: 0.05em;
              font-weight: 700;
              border-bottom: 1px solid $color-text02;
              width: 187px;
              margin: 10px auto;
              a {
                text-decoration: none;
                color: $color-text01;
              }
            }
            &.register {
              .text {
                @include font-line(16, 24);
                font-weight: 700;
                margin: 0 0 10px;
                em {
                  font-style: normal;
                  color: #f46669;
                }
                .if {
                  padding: 0 2px;
                }
              }
            }
            &.login {
              margin-top: 16px;
              border-top: 1px dashed $color-text01;
              padding-bottom: 16px;
              .text {
                @include font-line(14, 21);
                font-weight: 700;
                margin: 0 20px 10px;
                text-align: left;
              }
              .pass_forget {
                @include font-line(11, 22);
                letter-spacing: 0.05em;
                font-weight: 700;
                border-bottom: 1px solid $color-text02;
                width: 187px;
                margin: 10px auto;
                a {
                  text-decoration: none;
                  color: $color-text01;
                }
              }
            }
          }
          &.register {
            .text {
              @include font-line(16, 24);
              font-weight: 700;
              margin: 0 0 10px;
              em {
                font-style: normal;
                color: #f46669;
              }
              .if {
                padding: 0 2px;
              }
            }
          }
          &.login {
            margin-top: 16px;
            border-top: 1px dashed $color-text01;
            padding-bottom: 16px;
            .text {
              @include font-line(14, 21);
              font-weight: 700;
              margin: 0 20px 10px;
              text-align: left;
            }
            .pass_forget {
              @include font-line(11, 22);
              letter-spacing: 0.05em;
              font-weight: 700;
              border-bottom: 1px solid $color-text02;
              width: 187px;
              margin: 10px auto;
              a {
                text-decoration: none;
                color: $color-text01;
              }
            }
          }
          &.mypage {
            .text {
              @include font-line(15, 22.5);
              font-weight: 700;
              margin: 3px 0 14px;
            }
          }
          &.favorite {
            margin-top: 21px;
            border-top: 1px dashed $color-text01;
            padding-bottom: 20px;
            .text {
              @include font-line(15, 22.5);
              font-weight: 700;
              margin: 3px 0 14px;
            }
          }
        }
      }

      .private {
        width: 271px;
        height: 219px;
        position: absolute;
        bottom: 20px;
        text-align: center;
        z-index: 2;
        border: 1px solid $color-primary;
        background-color: #fff;
        &__title {
          @include font-line(15, 24);
          color: $color-text01;
          font-weight: 500;
          letter-spacing: 0.05em;
          margin: 24px 0 14px 0;
        }
        .button {
          @include font-line(16, 39);
          display: block;
          width: 170px;
          letter-spacing: 0.05em;
          margin: 0 auto;
          + .button {
            margin-top: 11px;
          }
          .fas {
            @include font-line(12);
            margin-right: 5px;
          }
        }
        &__logout {
          @include font-line(12, 20);
          letter-spacing: 0.05em;
          font-weight: 500;
          margin: 15px 0 0;
          a {
            color: $color-text01;
            text-decoration: underline;
          }
        }
      }

      .balloon {
        background: #fff;
        border: 2px solid $color-sub;
        border-radius: 10px;
        cursor: pointer;
        height: 72px;
        min-width: 139px;
        color: $color-sub;
        padding: 10px 20px;
        position: absolute;
        z-index: 2;
        .title {
          @include font-line(16, 16);
          letter-spacing: 0.05em;
          width: 100%;
          margin: 0;
        }
        .summary {
          @include font-line(16, 16);
          font-weight: 500;
          letter-spacing: 0.05em;
          margin-top: 6px;
          .member {
            padding-left: 5px;
          }
          .number {
            @include font-line(24, 24);
            display: inline-block;
            padding-right: 3px;
          }
          i {
            @include font-line(14, 20);
            width: 20px;
            margin-right: 8px;
            padding-left: 3px;
            border-radius: 50%;
            color: #fff;
            background: $color-sub;
            position: relative;
            text-align: center;
            top: -3px;
          }
        }
        &:hover {
          @include opacity(0.7);
        }
        &.kakogawa {
          top: 459px;
          left: 65px;
        }
        &.kanki {
          top: 331px;
          left: 186px;
        }
        &.sikata {
          top: 145px;
          left: 360px;
        }
        &.hamanomiya {
          top: 663px;
          left: 136px;
        }
        &.hioka {
          top: 233px;
          left: 413px;
        }
        &.hiraoka {
          top: 541px;
          left: 727px;
        }
        &.hiraokaminami {
          top: 661px;
          left: 675px;
        }
        &.chubu {
          top: 535px;
          left: 297px;
        }
        &.befu {
          top: 581px;
          left: 121px;
        }
        &.ryonan {
          top: 441px;
          left: 641px;
        }
        &.ryoso {
          top: 127px;
          left: 737px;
        }
        &.yamate {
          top: 321px;
          left: 769px;
        }
      }
    }
  }

  .bukkenlist {
    .tabs {
      margin: 0 auto 50px;
      padding: 0;
      display: flex;
      justify-content: space-between;
      .tab {
        display: inline-block;
        width: 200px;
        border-top: 1px solid #b6b6b6;
        border-bottom: 1px solid #b6b6b6;
        border-right: 1px solid #b6b6b6;
        background: #fff;
        .btn {
          background-color: transparent;
          border: none;
          width: 100%;
          padding: 0;
          font-weight: 700;
          @include font-line(15, 66);
          cursor: pointer;
          display: block;
          letter-spacing: 0.05em;
          text-align: center;
          position: relative;
          &:focus {
            outline: none;
          }
        }
        &:first-child {
          border-left: 1px solid #b6b6b6;
          .btn {
            font-family: 'Roboto', sans-serif;
            font-weight: 900;
          }
        }
        &:last-child {
        }
        &.is-active,
        &:hover {
          .btn {
            position: relative;
            border-bottom: 8px solid $color-main;
          }
        }
      }
    }
    .bukken {
      margin: 0 -35px 0 auto;
      padding: 0;
      display: flex;
      flex-flow: row wrap;
      &__item {
        margin: 0 32px 50px 0;

        &:nth-child(3n) {
          margin-right: 0;
        }
      }
    }
    .more {
      margin: 0 auto;
      .button {
        width: 500px;
        margin: 0 auto;
      }
    }
  }

  .pickup {
    padding: 80px 0 95px;
    &__heading {
      &__icon {
        margin-left: 40px;
        &::before {
          content: '';
          background: url(../img/icon_home_pickup.png) no-repeat;
          width: 34px;
          height: 32px;
          position: absolute;
          left: -40px;
          bottom: -10px;
        }
      }
    }
    #pickup-02,
    #pickup-03,
    #pickup-04,
    #pickup-05 {
      display: none;
    }
  }

  .pricedown {
    padding: 0 0 80px;
    &__heading {
      &__icon {
        margin-left: 50px;
        &::before {
          content: '';
          background: url(../img/icon_home_pricedown.png) no-repeat;
          width: 43px;
          height: 37px;
          position: absolute;
          left: -50px;
          bottom: -10px;
        }
      }
    }
    #pricedown-02,
    #pricedown-03,
    #pricedown-04,
    #pricedown-05 {
      display: none;
    }
  }

  .merit {
    background-color: #f8fff6;
    background-image:
      linear-gradient(transparent 96%,#f8fff6 4%),
      linear-gradient(90deg,transparent 96%,#f8fff6 4%),
      linear-gradient(transparent 50%,#cdead1 50%),
      linear-gradient(90deg,transparent 50%,#cdead1 50%);
    background-size: 26px 26px,26px 26px,2px 2px,2px 2px;
    background-position: 0 0, 0 0, 1px 1px, 1px 1px;
    background-repeat: repeat;
    padding: 109px 0 131px;
    position: relative;
    z-index: 1;
    &__list {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
    }
    &__item {
      width: 233px;
      height: 405px;
      margin: 0;
      padding: 23px 18px 0;
      position: relative;
      background: #fff;
      border: 1px solid #9d9d9d;
      box-shadow: 3px 5px 0 0 $color-main;
    }
    &__number {
      margin: auto;
      font-weight: 700;
      @include font-line(19, 19);
      color: #fff;
      font-family: "M PLUS Rounded 1c";
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background: $color-main;
      position: absolute;
      top: -25px;
      left: 10px;
      z-index: 1;
      display: flex;
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
      strong {
        @include font-line(36, 36);
      }
    }
    &__title {
      margin: 0 auto;
      font-weight: 800;
      @include font-line(18, 32);
      text-align: center;
      color: #1a8a2b;
      padding: 10px 0;
    }
    &__body {
      font-weight: 600;
      @include font-line(14, 23);
      padding: 0;
    }
    &__button {
      text-align: center;
      margin: 68px auto 0;
      .button {
        width: 700px;
      }
    }
  }

  .voice {
    padding: 80px 0;
    &__heading {
      &__icon {
        margin-left: 40px;
        &::before {
          content: '';
          background: url(../img/icon_home_voice.png) no-repeat;
          width: 35px;
          height: 41px;
          position: absolute;
          left: -40px;
          bottom: -10px;
        }
      }
    }
    &__list {
      display: flex;
    }
    &__item {
      flex: 0 0 185px;
      margin-right: 20px;
      padding: 10px;
      color: $color-text01;
      text-decoration: none;
      position: relative;
      overflow: hidden;
      transition: opacity 0.4s ease;
      border: 1px solid #a0a0a0;
      border-radius: 10px;
      &:hover {
        opacity: 0.75;
      }
      &:last-child {
        margin-right: 0;
      }
    }
    &__photo {
      width: 180px;
      height: 180px;
      margin: 0 auto 15px;
      overflow: hidden;
      position: relative;
      border-radius: 50%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        font-family: 'object-fit: cover;';
      }
    }
    &__title {
      margin: 5px auto 0;
      font-weight: 500;
      @include font-line(12, 20);
    }
    &__name {
      margin: 15px auto 0;
      font-weight: 700;
      @include font-line(15, 22);
    }
    &__button {
      text-align: center;
      margin-top: 40px;
      .button {
        width: 500px;
      }
    }
  }

  .column {
    $column: '.column';
    padding: 0 0 110px;
    position: relative;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      padding: 0 30px;
    }
    &__item {
      display: flex;
      flex-direction: column;
      position: relative;
    }
    &__left {
      flex: 0 0 505px;
    }
    &__right {
      flex: 0 0 400px;
    }
    &__title {
      margin: 0 40px 15px 65px;
      font-weight: 700;
      @include font-line(25, 25);
      font-family: "M PLUS Rounded 1c";
      text-align: center;
      height: 32px;
      border-bottom: 17px solid #cdead1;
      position: relative;
      &::before {
        content: '';
        background: url(../img/icon_home_column.png) no-repeat;
        width: 58px;
        height: 61px;
        position: absolute;
        left: -65px;
        top: -20px;
      }
    }
    &__text {
      margin: 0 auto auto;
      font-weight: 600;
      @include font-line(16, 30);
      letter-spacing: 0.04em;
      p {
        margin: 0 auto;
      }
    }
    &__photo {
      width: 100%;
      margin: 0 auto 45px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    &__button {
      width: 100%;
      margin: 30px auto 0;
      .button {
        width: 400px;
        height: 50px;
        border-radius: 25px;
        @include font-line(18, 18);
      }
    }
  }

  .sns {
    padding: 40px 0 80px;
    background: #cdead1;
    .sns-title {
      @include font-line(30, 30);
      font-family: 'Roboto',sans-serif;
      font-weight: 900;
      margin: 20px 0;
      &__sub {
        @include font-line(16, 24);
        font-family: 'Noto Sans Japanese',sans-serif;
        font-weight: 700;
        margin-left: 10px;
      }
      &__icon-insta,
      &__icon-fb {
        border-radius: 50%;
        height: 30px;
        width: 30px;
        margin-right: 7px;
        @include font-line(18, 30);
        color: #cdead1;
        text-align: center;
        position: relative;
        top: -4px;
      }
      &__icon-insta {
        background-color: #875024;
      }
      &__icon-fb {
        background-color: #2f3c75;
      }
    }
    &__inner {
      display: flex;
      justify-content: space-between;
      .instagram {
        flex: 0 0 470px;
        .mb_ins {
          margin-bottom: 46px;
        }
        .buttonBlock {
          width: 150px;
          margin: auto;
        }
      }
      .facebook {
        flex: 0 0 470px;
        .fb-page {
        }
        .mb_fb {
          margin-bottom: 45px;
        }
      }
    }
  }
}
