.p-step {
  &Description {
    margin: 0 35px 40px 35px;
    font-weight: 300;
    @include font-line(16, 30);
  }
  &Box {
    $step: &;
    border: 1px solid #494440;
    border-radius: 3px;
    margin-bottom: 40px;
    padding: 20px 0 20px 114px;
    position: relative;
    &__title {
      @include font-line(23, 34);
      font-weight: 700;
      display: flex;
      align-items: center;
      margin: 0;
      padding: 0 20px;
      position: relative;
      #{$step}__seal {
        @include font-line(18, 24);
        font-weight: 700;
        background-color: $color-primary;
        border-radius: 50%;
        color: #fff;
        display: inline-flex;
        align-items: center;
        flex-flow: column wrap;
        justify-content: center;
        height: 95px;
        width: 95px;
        position: absolute;
        bottom: -56px;
        left: -94px;
        em {
          @include font-line(36, 36);
          font-weight: 800;
          font-style: normal;
        }
      }
      #{$step}__titletext {
        flex: auto;
      }
    }
    &__body {
      margin-top: 10px;
      padding: 10px 60px 0 20px;
      border-top: 1px solid #494440;
      .days {
        padding-top: 20px;
        font-weight: 700;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
