.c-table01 {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin: 0;
  display: table;
  &__row {
    border-bottom: 1px solid #eaeaea;
    border-top: 1px solid #eaeaea;
    display: table-row;
  }
  &__heading {
    width: 20%;
    padding: 10px 0 10px 20px;
    font-weight: 700;
    @include font-line(15, 30);
    text-align: left;
    vertical-align: top;
    display: table-cell;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
    background-color: #eaeaea;
  }
  &__data {
    background-color: #fff;
    width: 80%;
    padding: 10px 0 10px 27px;
    font-weight: 500;
    @include font-line(15, 30);
    text-align: left;
    display: table-cell;
  }
}

