.c-table02 {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0;
  display: table;
  width: 100%;
  &__row {
    border-bottom: 1px dotted #aaaaaa;
    display: table-row;
  }
  &__heading {
    width: 70px;
    padding: 9px 0;
    font-weight: 700;
    @include font-line(14, 20);
    text-align: left;
    vertical-align: top;
    display: table-cell;
    position: relative;
  }
  &__data01 {
    width: auto;
    padding: 9px 0;
    font-weight: 700;
    @include font-line(20, 20);
    text-align: left;
    display: table-cell;
    strong {
      color: $color-sub;
      @include font-line(30, 30);
    }
  }
  &__data02 {
    width: auto;
    padding: 9px 0;
    font-weight: 500;
    @include font-line(14, 20);
    text-align: left;
    display: table-cell;
  }
}

