.c-table03 {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin: 0;
  display: table;
  &__row {
    border-bottom: 10px solid #fff;
    background-color: #f2f2f2;
    margin: 0;
    display: table-row;
    &:last-child {
      border-bottom: none;
      margin-bottom: 0;
    }
  }
  &__heading {
    width: 135px;
    padding: 18px;
    font-weight: 700;
    @include font-line(16, 24);
    text-align: left;
    vertical-align: top;
    display: table-cell;
    position: relative;
  }
  &__data {
    width: 215px;
    padding: 18px 20px 18px 0;
    font-weight: 500;
    @include font-line(16, 24);
    letter-spacing: 3px;
    vertical-align: top;
    text-align: left;
    display: table-cell;
  }
}

