  border-spacing: 0;
  margin: 0;
  display: table;
  width: 100%;
  &__row {
    border-bottom: 1px dashed #aaaaaa;
    display: table-row;
  }
  &__heading {
    width: 140px;
    padding: 9px 0;
    font-weight: 700;
    @include font-line(16, 20);
    text-align: left;
    vertical-align: top;
    display: table-cell;
    position: relative;
    i {
      @include font-line(20, 20);
      width: 25px;
      position: relative;
      top: 2px;
    }
    .fas {
      top: 0;
      left: 2px;
    }
  }
  &__data {
    width: auto;
    padding: 9px 0;
    font-weight: 500;
    @include font-line(16, 20);
    text-align: left;
    display: table-cell;
  }
}
